@media only screen and (min-width: 1024px){
    .form-container {
        margin: 5% 20%;
    }
}
@media only screen and (min-width: 768px) and (max-width: 1023px){
    .form-container {
        margin: 3% 20%;
    }
}
@media only screen and (min-width: 320px) and (max-width: 758px){
    .form-container {
        margin: 3% 5%;
    }
}

.form {
    background-color: #870D1D;
    border: 1px solid #DD2C49;
    border-radius: 10px;
}

.textarea-label {
    text-align: start;
    margin: 5%;
    color: white;
}

.button {
    background-color: #CBEDF2;
    margin: 5% 40%;
    width: 20%;
    height: 5%;
}

textarea {
    width: 90%;
    height: 150px;
    padding: 12px 20px;
    margin: 3% 5%;
    box-sizing: border-box;
    border-radius: 10px;
    border: 2px solid #870D1D;
    resize: none;
  }